import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { graphql, useStaticQuery } from 'gatsby';

import { MenuItems, ProductInfo, UrlLinks } from '@t/components';
import { createProductSectionData, isKorean, indexOf, isDescendantOf } from '@/util';
import { feGuideUrl, weeklyPickUrl } from '@/constant';
import { LogoLink } from '@/components/common/Link';
import { List } from '@/components/common/List';
import MenuItem from '@/components/layout/MenuItem';
import createGA from '@/components/common/createGA';
import SitemapSection from '@/components/main/sitemap/SitemapSection';

const MENU_INFOS = [
  ['application', true],
  ['component', true],
  ['tool', true],
  ['fe guide', false],
  ['weekly pick', false],
] as const;

const urlLinks: UrlLinks = {
  'fe guide': isKorean ? feGuideUrl.KO : feGuideUrl.EN,
  'weekly pick': isKorean ? weeklyPickUrl.KO : weeklyPickUrl.EN,
};

interface HeaderItems {
  application: ProductInfo[];
  component: ProductInfo[];
  tool: ProductInfo[];
}

interface HeaderProps {
  items: HeaderItems;
  setHeaderOpened: (isOpened: boolean) => void;
}

const Header = styled.div`
  position: fixed;
  padding-left: 240px;
  top: 0;
  left: 0;
  right: 0;
  height: 69px;
  background-color: #fff;
  z-index: 9999;
  border-bottom: 1px solid #d8d8d8;

  @media (max-width: 720px) {
    height: 112px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px 0 40px;
  }
`;

const Icon = styled.h1`
  display: inline-block;
  position: absolute;
  left: 60px;
  top: 23px;
  margin: 0;
  width: 152px;
  height: 24px;
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
  vertical-align: middle;

  @media (max-width: 1200px) {
    left: 30px;
  }

  @media (max-width: 720px) {
    position: static;
  }
`;

const NavBar = styled.div`
  display: inline-block;
  min-width: 500px;

  @media (max-width: 720px) {
    display: none;
  }
`;

const MenuList = styled(List)`
  white-space: nowrap;
`;

const MoreButton = styled.i`
  @media (max-width: 720px) {
    width: 52px;
    height: 52px;

    &.opened {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-mobile-header-more.png);
    }

    &.closed {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-mobile-header-close.png);
    }
  }
`;

const MobileHeaderMenu = styled.div`
  display: none;
  position: fixed;
  top: 56px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  background-color: #fff;

  & .sitemap-logo {
    display: none;
  }

  @media (max-width: 720px) {
    &.closed {
      display: block;
      overflow: auto;
    }
  }
`;

const getMenuItemProps = (items: HeaderItems): MenuItems[] =>
  MENU_INFOS.map((menuInfo) => {
    const [type] = menuInfo;

    if (menuInfo[1]) {
      return {
        type,
        subMenu: items[menuInfo[0]],
      };
    }

    return {
      type,
      link: urlLinks[menuInfo[0]],
    };
  });

const HeaderComponent: React.FC<HeaderProps> = ({ items, setHeaderOpened }) => {
  const { allProductsJson } = useStaticQuery(
    graphql`
      query headerQuery {
        # query for each section
        ...allProductsJson
      }
    `
  );

  const [index, setIndex] = useState<number>(-1);
  const [clicked, setClicked] = useState<boolean>(false);
  const [isMobileHeaderOpened, setMobileHeaderOpened] = useState<'opened' | 'closed'>('opened');

  const showSubMenu = (ev: React.MouseEvent): void => {
    const target = (ev.target as HTMLElement).getAttribute('data-index')
      ? ev.target
      : ev.currentTarget;

    setIndex(parseInt((target as HTMLElement).getAttribute('data-index') ?? '0', 10));
    setClicked(false);
  };
  const hideSubMenu = (): void => {
    setIndex(-1);
    setClicked(false);
  };
  const clickSubMenu = ({ target }: React.MouseEvent<HTMLElement>): void => {
    if ((target as HTMLElement).className === 'link') {
      setClicked(true);
    }
  };
  const isShowSubMenu = (idx: number): boolean => index === idx && !clicked;
  const clickMobileButton = (): void => {
    setMobileHeaderOpened(isMobileHeaderOpened === 'opened' ? 'closed' : 'opened');
    setHeaderOpened(isMobileHeaderOpened === 'opened');
  };
  const clearShowState = (): void => {
    setMobileHeaderOpened('opened');
    setHeaderOpened(false);
  };

  const headerMenuItems: MenuItems[] = getMenuItemProps(items);
  const ga = createGA('Header', 'click', 'header logo', 'logo');

  const sitemapData = createProductSectionData(allProductsJson);
  const mobileGa = createGA('Header - sitemap', 'click', 'sitemap');

  const content = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (content.current) {
      const anchors = content.current.querySelectorAll('a');

      content.current.addEventListener('click', ({ target }) => {
        if (
          anchors &&
          (indexOf(anchors, target as HTMLElement) !== -1 ||
            isDescendantOf(anchors, target as HTMLElement))
        ) {
          clearShowState();
        }
      });
    }
  }, [content]);

  return (
    <Header data-testid="header" onClick={clickSubMenu}>
      <Icon onClick={clearShowState}>
        <LogoLink url="/" ga={ga}>
          TOAST UI
        </LogoLink>
      </Icon>
      <NavBar>
        <MenuList>
          {headerMenuItems.map((menu, idx) => (
            <MenuItem
              menu={menu}
              showSubMenu={showSubMenu}
              hideSubMenu={hideSubMenu}
              isShowSubMenu={isShowSubMenu}
              index={idx}
              key={idx}
            />
          ))}
        </MenuList>
      </NavBar>
      <MoreButton className={isMobileHeaderOpened} onClick={clickMobileButton} />
      <MobileHeaderMenu ref={content} className={isMobileHeaderOpened}>
        <SitemapSection items={sitemapData} ga={mobileGa} />
      </MobileHeaderMenu>
    </Header>
  );
};

export default HeaderComponent;
