import React from 'react';
import GatsbyLink from 'gatsby-link';
import styled from 'styled-components';
import ReactGA from 'react-ga';

import { GA } from '@t/components';

interface LinkProps {
  type: 'external' | 'internal';
  url: string;
  ga: GA;
  className?: string;
  testId?: string;
  onClick?: () => void;
}

function noop() {
  // do nothing
}

export const Link: React.FC<LinkProps> = ({
  type,
  url,
  ga,
  children,
  className = '',
  testId = '',
  onClick = noop,
}) => {
  if (type === 'external') {
    return (
      <a
        href={url}
        target="_blank"
        rel="noopener noreferrer"
        className={className}
        data-testid={testId}
        onClick={() => {
          ReactGA.event(ga);
          onClick();
        }}
      >
        {children}
      </a>
    );
  }

  return (
    <GatsbyLink
      to={url}
      onClick={() => {
        ReactGA.event(ga);
        onClick();
      }}
    >
      {children}
    </GatsbyLink>
  );
};

const Logo = styled.div`
  background-image: url('https://uicdn.toast.com/toastui/img/icons.png');
  background-position: -34px -22px;
`;

export const LogoLink: React.FC<Pick<LinkProps, 'url' | 'ga' | 'onClick'>> = ({
  url,
  ga,
  children,
  onClick = noop,
}) => (
  <GatsbyLink
    to={url!}
    style={{ display: 'block' }}
    onClick={() => {
      ReactGA.event(ga);
      onClick();
    }}
  >
    <Logo>{children}</Logo>
  </GatsbyLink>
);

