import React from 'react';
import styled from 'styled-components';

import { ProductType, MenuTitleType, ProductInfo, ProductTitleType, GA } from '@t/components';
import { SitemapTitle, SitemapTitleWithSubMenu } from '@/components/main/sitemap/SitemapTitle';

interface SitemapSectionProps {
  items: SitemapInfo;
  ga: GA;
}

interface SitemapInfo {
  application: ProductInfo[];
  component: ProductInfo[];
  tool: ProductInfo[];
}

interface SitemapItem {
  type?: ProductType;
  title: MenuTitleType;
}

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 80px 0;
  margin: 0 auto;
  max-width: 1200px;
  width: 62.5%;

  @media (max-width: 1200px) {
    padding-top: 50px;
    max-width: 900px;
    width: 75%;
  }

  @media (max-width: 768px) {
    padding-top: 40px;
    max-width: 700px;
  }

  @media (max-width: 720px) {
    max-width: 660px;
    flex-direction: column;
    padding: 0;
  }
`;

const SitemapLogo = styled.img`
  height: 22px;

  @media (max-width: 1200px) {
    width: 100px;
    height: 18px;
  }

  @media (max-width: 720px) {
    width: 170px;
    height: 28px;
    margin-top: 90px;
    margin-bottom: 57px;
  }
`;

const SitemapArea = styled.div`
  width: 700px;

  @media (max-width: 1200px) {
    width: 500px;
    margin-right: 0;
  }

  @media (max-width: 720px) {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
`;

const SitemapSection: React.FC<SitemapSectionProps> = ({ items, ga }) => {
  const sitemapItems: SitemapItem[] = [
    {
      type: 'application',
      title: 'Applications',
    },
    {
      type: 'component',
      title: 'Components',
    },
    {
      type: 'tool',
      title: 'Tools',
    },
    {
      title: 'FE Guide',
    },
    {
      title: 'Weekly Picks',
    },
  ];

  return (
    <Wrapper>
      <div className="sitemap-logo">
        <SitemapLogo src="https://uicdn.toast.com/toastui/img/toast-ui-black-logo.png" />
      </div>
      <SitemapArea>
        {sitemapItems.map(({ type, title }) =>
          type ? (
            <SitemapTitleWithSubMenu
              items={items[type]}
              title={title as ProductTitleType}
              ga={{ ...ga, label: `sitemap - ${title}` }}
              key={title}
            />
          ) : (
            <SitemapTitle
              title={title as Exclude<MenuTitleType, ProductTitleType>}
              ga={{ ...ga, label: `sitemap - ${title}` }}
              key={title}
            />
          )
        )}
      </SitemapArea>
    </Wrapper>
  );
};

export default SitemapSection;
