import React, { useState } from 'react';
import styled from 'styled-components';

import { ProductInfo, ProductTitleType, MenuTitleType, GA } from '@t/components';
import { isKorean } from '@/util';
import { feGuideUrl, weeklyPickUrl } from '@/constant';
import { BlockLink, InlineBlockLink } from '@/components/common/Link';
import createGA from '@/components/common/createGA';

interface SitemapTitleWithSubMenuProps {
  items: ProductInfo[];
  title: ProductTitleType;
  ga: GA;
}

interface SitemapTitleProps {
  title: Exclude<MenuTitleType, ProductTitleType>;
  ga: GA;
}

const urlMap = {
  'FE Guide': isKorean ? feGuideUrl.KO : feGuideUrl.EN,
  'Weekly Picks': isKorean ? weeklyPickUrl.KO : weeklyPickUrl.EN,
};

const Wrapper = styled.div`
  margin-right: 100px;
  float: left;

  @media (max-width: 1200px) {
    margin-right: 70px;
  }

  @media (max-width: 720px) {
    margin-right: 0;
    cursor: pointer;

    &.opened {
      margin-bottom: 0;
    }

    &.closed {
      margin-bottom: 50px;
      height: 50px;
    }
  }
`;

const TitleArea = styled.div`
  height: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media (max-width: 720px) {
    height: 48px;
  }
`;

const Title = styled.div`
  display: inline-block;
  width: 100px;
  margin-bottom: 30px;

  &:hover {
    text-decoration: underline;
  }

  @media (max-width: 1200px) {
    width: 75px;
    margin-bottom: 20px;
  }

  @media (max-width: 720px) {
    width: 100%;
    height: 50px;
    line-height: 50px;
    margin-bottom: 50px;
    cursor: pointer;
  }
`;

const MenuTitle = styled.span`
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: #222;
  white-space: nowrap;

  @media (max-width: 1200px) {
    font-size: 12px;
    line-height: 18px;
  }

  @media (max-width: 720px) {
    font-size: 28px;
    line-height: 24px;
  }
`;

const Menu = styled.span`
  font-size: 14px;
  line-height: 30px;
  color: #777;
  white-space: nowrap;

  &:hover {
    text-decoration: underline;
  }

  @media (max-width: 1200px) {
    font-size: 10px;
    line-height: 23px;
  }

  @media (max-width: 720px) {
    font-size: 24px;
    line-height: 50px;
  }
`;

const MoreButton = styled.i`
  @media (max-width: 720px) {
    width: 48px;
    height: 48px;

    &.opened {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-btn-more-close.png);
    }

    &.closed {
      background: url(https://uicdn.toast.com/toastui/img/toast-ui-btn-more-open.png);
    }
  }
`;

const SubMenu = styled.div`
  margin: 15px 0;
  display: flex;
  flex-direction: column;

  @media (max-width: 720px) {
    margin: 40px 0;
    padding-left: 20px;

    &.closed {
      display: none;
    }
  }
`;

export const SitemapTitleWithSubMenu: React.FC<SitemapTitleWithSubMenuProps> = ({
  items,
  title,
}) => {
  const [className, setClassName] = useState<'opened' | 'closed'>('closed');
  const clickMoreButton = (): void => setClassName(className === 'opened' ? 'closed' : 'opened');
  const ga = createGA('Main - Sitemap', 'click', 'Sitemap');

  return (
    <Wrapper className={className}>
      <TitleArea onClick={clickMoreButton}>
        <MenuTitle>{title}</MenuTitle>
        <MoreButton className={className} />
      </TitleArea>
      <SubMenu className={className}>
        {items.map(({ id, name, link }) => {
          const type = title !== 'Tools' ? 'internal' : 'external';
          const url =
            title !== 'Tools' ? `/${id}` : link || `https://github.com/nhn/${id.replace('-', '.')}`;

          return (
            <BlockLink
              key={id}
              type={type}
              url={url}
              ga={{ ...ga, label: `Sitemap - ${name}` }}
              onClick={() => setClassName('closed')}
              testId="sitemap-submenu-list"
            >
              <Menu>{name}</Menu>
            </BlockLink>
          );
        })}
      </SubMenu>
    </Wrapper>
  );
};

export const SitemapTitle: React.FC<SitemapTitleProps> = ({ title }) => {
  const ga = createGA('Main - Sitemap', 'click', 'Sitemap', title);

  return (
    <Title>
      <InlineBlockLink
        type="internal"
        url={`${urlMap[title]}`}
        ga={{ ...ga, label: `Sitemap - ${title}` }}
        testId="sitemap-list"
      >
        <MenuTitle>{title}</MenuTitle>
      </InlineBlockLink>
    </Title>
  );
};
